.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}
.caregiverImg{
  height:100%;
  border: 10px solid grey;
  border-radius: 25px;
}
.primaryCaregiver{
  max-height: auto;
}
.babysitter{
  max-height:2em;
}


@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}